.container {
    text-align: center;
    width: 100%;
    font-family: 'Poppins', sans-serif; 
    padding-top: 30px;
   
}

.container h1 {
    font-size: 23px;
    font-weight: 500;
}

.container p {
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    display: -webkit-box;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.5;
    max-height: 3em;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: normal;
}

.flashcardContainer {
    display: flex;
    overflow-x: auto;
    
    gap: 20px;
    justify-content: flex-start;
    position: relative;
    scrollbar-width: thin;
    scrollbar-color: black transparent;
}

.flashcardContainer::-webkit-scrollbar {
    height: 4px;
    width: 4px;
}

.flashcardContainer::-webkit-scrollbar-thumb {
    background-color: black;
    border-radius: 2px;
}

.flashcardContainer::-webkit-scrollbar-track {
    background: transparent;
}

.flashcard {
    flex: 0 0 30%;
    max-width: 30%;
    min-height: 250px;
    background: #fff;
    border: 1px solid #ccc;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
    border-radius: 10px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.imageContainer {
    height: 180px;
    background-size: cover;
    background-position: center;
}

.textContainer {
    padding: 8px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.flashcardTitle, .flashcardDescription {
    margin: 0;
    padding: 2px 5px;
}

.flashcardTitle {
    font-size: 16px;
    font-weight: 600;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.flashcardDescription {
    font-size: 14px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    display: -webkit-box;
    -webkit-box-orient: vertical;
}

@media (max-width: 600px) {
    .flashcard {
        flex: 0 0 100%;
        max-width: 100%;
        padding: 8px;
    }

    .imageContainer {
        height: 140px;
    }

    .textContainer {
        padding: 5px;
    }

    .flashcardTitle, .flashcardDescription {
        font-size: 14px; 
    }
}

.arrowButton {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 30px;
    height: 30px;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border-radius: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    z-index: 10;
}

.arrowButton.left {
    left: 10px;
}

.arrowButton.right {
    right: 10px;
}

.arrowButton:hover {
    background-color: rgba(0, 0, 0, 0.8);
}

.container h1 {
    font-size: 23px;
    font-weight: 500;
}
